.navbar-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1em;
}

.logo {
  position: relative;
  width: 4rem;
  height: auto;
}

.buttons-container {
  display: flex;
  gap: 0.6rem;
}

a {
  all: unset;
}


@media (max-width: 40em) {

  .buttons-container {
    gap: 0.5em;
  }

}


