@font-face {
  font-family: 'MainFontFamily';
  src: local('MainFontFamily'), url(./assets/Poppins-Regular.ttf) format('truetype');
}

html {
  background: url(./assets/background/background.jpg) no-repeat;
  background-size: auto;
}

:root {
  --primary-color: 21, 0, 80;
  --secondary-color: 63, 0, 113;
  --third-color: 255, 88, 88;

  --text-primary-color: #fff;
  --text-secondary-color: #000000;

  --desktop-gap: 5rem;
  --mobile-gap: 3rem;
  --desktop-padding: 5rem;
  --mobile-padding: 3rem;

  --desktop-section-image-width: 20rem;
  --mobile-section-image-width: 15rem;

  --text-color-code-one: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: MainFontFamily;

  -webkit-user-drag: none;
  -webkit-tap-highlight-color: transparent;
}

html {
  font-size: 1.3em;
}

.container-main {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
}

/* Handle small screens */
@media (max-width: 40rem) {

  html {
    font-size: 1.1em;
  }

}

/* Handle big screens */
@media (min-width: 1950px) {

  html {
    background-size: 100%;
    font-size: 2em;
  }

}