/* Button */
.button-constant {
    list-style: none;
    text-decoration: none;
    border-radius: 1em;
    cursor: pointer;
    color: var(--text-primary-color);
    border: 0.1em solid var(--text-primary-color);
    background-color: rgb(var(--primary-color));
    user-select: none;
    margin-top: 0.60em;
    padding: 0.2em 0.5em;
}

.button-constant:hover {
  background-color: rgb(var(--third-color));
}